"use client";
import Link from "next/link";
import { useEffect, useState } from "react";
import { User } from "lucide-react";

export function Topbar() {
  // horloge locale
  const [now, setNow] = useState(new Date());
  useEffect(() => {
    const id = setInterval(() => setNow(new Date()), 1_000);
    return () => clearInterval(id);
  }, []);

  return (
    <header className="h-14 px-4 sm:px-6 bg-brand flex items-center justify-between text-white">
      {/* logo cliquable (accueil) + version */}
      <div className="flex items-center gap-3">
        <Link href="/" className="flex items-center">
          <img src="/logo_stirweld.svg" className="h-8" alt="Stirweld" />
        </Link>
        <span className="text-sm font-semibold">â–¼ V4 v2.0</span>
      </div>

      {/* date / heure */}
      <div className="text-right leading-none font-semibold">
        <div>{now.toLocaleTimeString("en-US")}</div>
        <div>{now.toLocaleDateString("en-US")}</div>
      </div>

      {/* user + Exit */}
      <div className="flex items-center gap-4">
        <div className="flex flex-col items-center text-xs">
          <User className="h-8 w-8" />
          <span>SystemUser</span>
        </div>
        <button className="rounded bg-brand-accent px-3 py-1 text-sm">
          Exit HMI
        </button>
      </div>
    </header>
  );
}
